         
    div#user-agreement-container {
      background-color: rgba(0,184,184,0.1);
      width: 950px;

      display: inline-block;
      vertical-align: middle;
      outline: 0;
      margin-left: 45px;
      padding-top: 10px;
      overflow: hidden;
      vertical-align: middle
         
    }
    div {
          overflow: hidden;
          vertical-align: middle
    }

    input[type=checkbox]+label {
      display: inline-block;
      vertical-align: middle;
      background: #fff;
      width: 28px;
      height: 28px;
      border: 2px solid #D94185;
      position: relative;
      visibility:visible;
     
    }

     input[type=checkbox]:checked+label {
      display: inline-block;
      vertical-align: middle;
      border: 3px solid #1ac7c7;
      width: 28px;
      height: 28px;
      position: relative;
      visibility:visible;
      background: rgba(0,184,184,0.1);
     
    }

    input[type=checkbox]:checked+label:after {
        content: '';
        position: absolute;
        width: 12px;
        height: 5px;
        background: transparent;
        top: 8px;
        left: 6px;
        border: 3px solid #1ac7c7;
        border-top: none;
        border-right: none;
        
        -webkit-transform: rotate(-45deg);
        
            -ms-transform: rotate(-45deg);
        
                transform: rotate(-45deg);
       
        opacity: 1;
    }

    .user_agreement_check {
      visibility:hidden;
      cursor:pointer;

    }

    .item-info {
           display: inline-block;
           vertical-align: middle;
           outline: 0;
           margin-left: 45px;
           padding-left: 30px; 
           padding-bottom: 80px;
           font-size: 12px;
          display: inline-block;
          vertical-align: middle;
          color: #999;   
    }

         
    
    .requiredMessage > span  {
      color: #87909A;
      font-size: 10px;

    }
           
     .user-agreement-checkbox >  label span {
      vertical-align: middle;
    }

  
    .text-small-bold  {
       font-weight: bold; 
       color: #999;
    }

    div.user-agreement-checkbox a:visited {
      color: #085F99;
      text-decoration: underline;
      cursor: pointer;
    }
   


